import { relations, sql } from "drizzle-orm";
import { pgTable } from "drizzle-orm/pg-core";

import { user } from "./auth-schema";

export const Video = pgTable("video", (t) => ({
  id: t.uuid().notNull().primaryKey().defaultRandom(),
  title: t.varchar({ length: 256 }).notNull(),
  description: t.text(),

  userId: t
    .text("user_id")
    .notNull()
    .references(() => user.id),
  url: t.text(),

  createdAt: t.timestamp().defaultNow().notNull(),
  updatedAt: t
    .timestamp({ mode: "date", withTimezone: true })
    .$onUpdateFn(() => new Date()),
}));

export const VideoComment = pgTable("video_comment", (t) => ({
  id: t.uuid().notNull().primaryKey().defaultRandom(),

  content: t.text(),

  startTime: t.real(),
  endTime: t.real(),

  createdAt: t.timestamp().defaultNow().notNull(),
  updatedAt: t
    .timestamp({ mode: "date", withTimezone: true })
    .$onUpdateFn(() => sql`now()`),

  videoId: t
    .uuid("video_id")
    .notNull()
    .references(() => Video.id, {
      onDelete: "cascade",
    }),

  userId: t
    .text("user_id")
    .notNull()
    .references(() => user.id),
}));

export const videoCommentRelations = relations(VideoComment, ({ one }) => ({
  video: one(Video, {
    fields: [VideoComment.videoId],
    references: [Video.id],
  }),
  user: one(user, {
    fields: [VideoComment.userId],
    references: [user.id],
  }),
}));

export const workspace = pgTable("workspace", (t) => ({
  id: t.uuid().notNull().primaryKey().defaultRandom(),
  name: t.varchar({ length: 256 }).notNull(),
}));

export * from "./auth-schema";
